import React, {useEffect, useState} from 'react'
import { db } from '../Config/Config'
import { collection, getDocs } from "firebase/firestore";
import { useTable } from 'react-table'

export const Admin = () => {

    const [orders, setOrders] = useState([])
    useEffect(() => {
        const querySnapshot = getDocs(collection(db, `Orders`)).then((querySnapsot) => {
            querySnapsot.forEach((doc) => {
                let order = doc.data()
                console.log(order)
                setOrders((prev) => [...prev, order])
              });
        })
    },[])

    function Table({ columns, data }) {
        // Use the state and functions returned from useTable to build your UI
        const {
          getTableProps,
          getTableBodyProps,
          headerGroups,
          rows,
          prepareRow,
        } = useTable({
          columns,
          data,
        })
      
        console.log(data)

        // Render the UI for your table
        return (
          <table {...getTableProps()} className='tableWrapper'>
            <thead>
              {headerGroups.map(headerGroup => (
                <tr {...headerGroup.getHeaderGroupProps()} style={{border:'1px solid red'}}>
                  {headerGroup.headers.map(column => (
                    <th {...column.getHeaderProps()}>{column.render('Header')}</th>
                  ))}
                </tr>
              ))}
            </thead>
            <tbody {...getTableBodyProps()}>
              {rows.map((row, i) => {
                prepareRow(row)
                return (
                  <>
                    <tr {...row.getRowProps()}>
                        {row.cells.map(cell => {
                            return <td {...cell.getCellProps()} style={{ border: '1px solid #ededed', borderTopWidth: '0', borderBottomWidth: '0' }}>{cell.render('Cell')}</td>;
                        })}
                        {/* <table style={{width:'100%'}}>
                            <thead>
                                <tr>
                                    <td>Name</td>
                                    <td>Price</td>
                                    <td>Quantity</td>
                                    <td>Side</td>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>xxxbody</td>
                                    <td>xxxbody</td>
                                    <td>xxxbody</td>
                                    <td>xxxbody</td>
                                </tr>
                            </tbody>
                        </table> */}
                    </tr>
                </>
                )
              })}
            </tbody>
          </table>
        )
      }

      const columns = [
          {
            Header: 'Oreders',
            columns: [
              {
                Header: 'First Name',
                accessor: 'BuyerFirstName',
              },
              {
                Header: 'Last Name',
                accessor: 'BuyerLastName',
              },
              {
                Header: 'Email',
                accessor:'BuyerEmail',
              },
              {
                Header: 'Phone',
                accessor:'BuyerCell',
              },
              {
                Header: 'Postcode',
                accessor: 'BuyerPostcode',
              },
              {
                Header: 'State',
                accessor: 'BuyerState',
              },
              {
                Header: 'City',
                accessor: 'BuyerCity',
              },
              {
                Header: 'Address',
                accessor: 'BuyerAddress',
              },
              {
                Header: 'Apartament',
                accessor: 'BuyerApartamentNumber',
              },
              {
                Header: 'Payment',
                accessor: 'BuyerPayment',
              },
              {
                Header: 'Date',
                accessor: 'DateOfShop',
              },
              {
                Header: 'Goods',
                accessor: data => (
                <table style={{width:'100%'}}>
                    <thead>
                        <td>Name</td>
                        <td>Price</td>
                        <td>Quantity</td>
                        <td>Side</td>
                    </thead>
                    <tbody>
                        <tr>
                            {
                                data.BuyerGoods.map(item => (
                                    <><td>{item.name}</td><td>{item.price}</td><td>{item.qty}</td><td>{item.side}</td></>
                                  ))
                            }
                        </tr>
                    </tbody>
                </table>
                )
              },
            ],
          }
        ]

    return (
        <div>
            <Table columns={columns} data={orders} />   
        </div>

    )
}
